import {
   AfterViewInit,
   ComponentFactoryResolver,
   Directive,
   ElementRef,
   Host,
   Input,
   Optional,
   Renderer2,
   Self,
} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {NavigationService} from "@navigation/navigation.service";
import {ClrButtonBaseDirective} from "@directive/clr-button-group/clr-button-base.directive";
import {ClrButton} from "@clr/angular";

/**
 * Decorates a button or an anchor as external link. Requires one of the optional parameters articleId, helpId or url.
 *
 * @param external-link: Optional. Localization key of the text label of the external link. If omitted default label
 * will be used - "Learn more" for anchors or "Ask VMWare" for buttons.
 * @param articleId - Optional. Id of article in knowledge base.
 * @param helpId - Optional. Help id which will redirect to Knowledge Base.
 * @param url - Optional. External URL.
 */
@Directive({
   selector: "a[external-link], button[external-link], clr-button[external-link]",
})
export class ExternalLinkDirective extends ClrButtonBaseDirective implements AfterViewInit {
   private readonly ASK_VMWARE_DEFAULT_TEXT_KEY: string = "vsan.common.askVMware";
   private readonly LEARN_MORE_DEFAULT_TEXT_KEY: string = "vsan.common.learnMore";

   private readonly CONTAINER_CLASS: string = "icon-name-container";
   private readonly NAVIGATION_EVENT_TYPE: string = "click";
   private readonly LEAD_TO_NOWHERE_URL: string = "javascript://";
   private readonly HTML_ELEMENT_SPAN_NAME: string = "span";
   private readonly HTML_ELEMENT_ICON_NAME: string = "clr-icon";
   private readonly HTML_ELEMENT_ROLE_ATTR_NAME: string = "role";
   private readonly HTML_ELEMENT_ROLE_ATTR_VALUE: string = "link";
   private readonly ICON_SHAPE_ATTR_NAME: string = "shape";
   private readonly ICON_SHAPE_ATTR_VALUE: string = "pop-out";
   private readonly ICON_SIZE_ATTR_NAME: string = "size";
   private readonly ICON_SIZE_ATTR_VALUE: string = "16";
   private readonly ICON_CLASS_ATTR_NAME: string = "class";
   private readonly ICON_CLASS_ATTR_VALUE: string = "link";
   private readonly ICON_TITLE_ATTR_NAME: string = "title";

   private readonly KB_URL: string = "http://kb.vmware.com/kb/";

   @Input("external-link")
   anchorText: string;

   @Input()
   articleId: string;

   @Input()
   helpId: string;

   @Input()
   url: string;

   constructor(private el: ElementRef<HTMLElement>,
         private resolver: ComponentFactoryResolver,
         private renderer: Renderer2,
         private navigationService: NavigationService,
         // this variable will be initialized only if the host is clr-button
         @Host() @Self() @Optional() clrButton: ClrButton) {
      super(clrButton);
   }

   ngAfterViewInit(): void {
      const element: HTMLElement = this.el.nativeElement;
      // continue only if element is HTML link or button
      if (!this.isLink(element) && !this.isButton(element)) {
         // element is clr-button, wait until html button for it is resolved - handled by resloveButton
         return;
      }
      this.prepareExternalLink(element);
   }

   resolveButton(clrButton: ClrButton, button: HTMLButtonElement, index: number): void {
      // only if element is clr-button. At this point the html button is already resolved. Apply on it.
      this.prepareExternalLink(button);
   }

   private prepareExternalLink(element: HTMLElement): void {
      this.renderer.addClass(element, this.CONTAINER_CLASS);

      this.setText(element);
      if (this.isLink(element)) {
         this.decorateLink(element);
      } else {
         this.addLinkRole(element);
      }

      this.initializeNavigationHandler(element);
   }

   private isButton(element: HTMLElement): boolean {
      return element instanceof HTMLButtonElement;
   }

   private isLink(element: HTMLElement): boolean {
      return element instanceof HTMLAnchorElement;
   }

   private setText(element: HTMLElement): void {
      const span: HTMLSpanElement = this.renderer.createElement(this.HTML_ELEMENT_SPAN_NAME);
      span.innerText = this.getText(element);
      this.renderer.appendChild(element, span);

      const icon: HTMLSpanElement = this.renderer.createElement(this.HTML_ELEMENT_ICON_NAME);
      icon.setAttribute(this.ICON_SHAPE_ATTR_NAME, this.ICON_SHAPE_ATTR_VALUE);
      icon.setAttribute(this.ICON_SIZE_ATTR_NAME, this.ICON_SIZE_ATTR_VALUE);
      icon.setAttribute(this.ICON_CLASS_ATTR_NAME, this.ICON_CLASS_ATTR_VALUE);
      // Set title of the icon for screen readers clarity
      icon.setAttribute(this.ICON_TITLE_ATTR_NAME, VsanUiUtils.getString("vsan.screenReader.label.externalLink.icon"));
      this.renderer.appendChild(element, icon);
   }

   private getText(element: HTMLElement): string {
      if (this.anchorText) {
         return this.anchorText;
      }
      const key: string = this.isButton(element) ? this.ASK_VMWARE_DEFAULT_TEXT_KEY : this.LEARN_MORE_DEFAULT_TEXT_KEY;
      return VsanUiUtils.getString(key);
   }

   private decorateLink(element: HTMLElement): void {
      const linkElement: HTMLLinkElement = element as HTMLLinkElement;
      // don't use href attribute. If omitted, the link will be not visualized properly.
      linkElement.href = this.LEAD_TO_NOWHERE_URL;
      // display link on same line
      linkElement.style.display = "inline-flex";
   }

   private initializeNavigationHandler(element: HTMLElement): void {
      element.addEventListener(this.NAVIGATION_EVENT_TYPE, () => {
         if (this.helpId) {
            this.navigationService.askVMware(this.helpId);
            return;
         }
         if (this.articleId) {
            this.navigationService.navigateToAddress(`${this.KB_URL}${this.articleId}`);
            return;
         }
         if (this.url) {
            this.navigationService.navigateToAddress(this.url);
         }
      });
   }

   // Indicate external navigation by assigning "link" role
   private addLinkRole(element: HTMLElement): void {
      this.renderer.setAttribute(element, this.HTML_ELEMENT_ROLE_ATTR_NAME, this.HTML_ELEMENT_ROLE_ATTR_VALUE);
   }
}