import {AfterViewInit, Directive, ElementRef, Input, Renderer2} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * Attribute directive for setting a custom title to the clr-icon inside a clr-signpost.
 * It also adds aria-label with the same value and aria-expanded="false" to the button, holding the icon.
 * Used for changing the title when a clr-icon is not explicitly added inside a clr-signpost.
 */
@Directive({
   selector: "clr-signpost",
})
export class SignpostTitleDirective implements AfterViewInit {
   private readonly BUTTON_TAG_NAME: string = "button";
   private readonly CLR_ICON_TAG_NAME: string = "clr-icon";

   private readonly TITLE_ATTRIBUTE: string = "title";
   private readonly ARIA_EXPANDED_ATTRIBUTE: string = "aria-expanded";
   private readonly ARIA_LABEL_ATTRIBUTE: string = "aria-label";

   @Input("vsan-signpost-title")
   signpostTitle: string;

   constructor(private el: ElementRef<HTMLElement>,
         private renderer: Renderer2) {
   }

   ngAfterViewInit(): void {
      if (!this.signpostTitle) {
         return;
      }

      const signpostIcon: Element = this.el.nativeElement.getElementsByTagName(this.CLR_ICON_TAG_NAME).item(0);
      if (signpostIcon) {
         this.renderer.setAttribute(signpostIcon, this.TITLE_ATTRIBUTE, VsanUiUtils.getString(this.signpostTitle));
      }
      const button: Element = this.el.nativeElement.getElementsByTagName(this.BUTTON_TAG_NAME).item(0);
      if (button) {
         this.renderer.setAttribute(button, this.ARIA_EXPANDED_ATTRIBUTE, "false");
         this.renderer.setAttribute(button, this.ARIA_LABEL_ATTRIBUTE, VsanUiUtils.getString(this.signpostTitle));
      }
   }
}
